body {
  max-width: 768px;
  margin: 0 auto;
  background-color: #f6f8f9;
}
.has_header_pt {
  padding-top: 1.45rem;
}
.has_footer_pb {
  padding-bottom: 1.3rem;
}

/* 按钮样式start */
.ui-button-block {
  display: block;
}
.ui-button-100per {
  width: 100%;
}

/* 高度 */
.ui-button-size-h70 {
  padding: 0rem 0.22rem;
  height: 0.7rem;
}
.ui-button-size-h70 .ui-button-content {
    font-size: 0.3rem;
    vertical-align: bottom;
    margin-left: 0.1rem;
}
.ui-button-size-h130 {
  height: 1.3rem;
  line-height: 1.3rem;
}
.ui-button-size-h130 .ui-button-content {
    font-size: 0.48rem;
}

/* 背景颜色*/
.ui-button-bg-ed5 {
  background-color: #ed5757;
  color: #fff;
}
.ui-button-bg-73a {
  background-color: #73afff;
  color: #fff;
}
.ui-button-bg-e9f {
  background-color: #e9f4fe;
  color: #a3bfd4;
}
.ui-button-bg-63c {
  background-color: #63c961;
  color: #fff;
}
.ui-button-bg-3b9 {
  background-color: #3b91ff;
  color: #fff;
}

/* 圆角值 */
.ui-button-min-border-radius {
  border-radius: 0.08rem;
}
.ui-button .icon {
  display: inline-block;
  vertical-align: bottom;
}
.ui-button .icon.revoke-icon {
    width: 0.38rem;
    height: 0.38rem;
    background-image: url("./images/common/back-icon-37x31.png");
    background-repeat: no-repeat;
    background-size: contain;
}
.ui-button .icon.cancle-icon {
    width: 0.38rem;
    height: 0.38rem;
    background-image: url("./images/common/delete-icon-34x35.png");
    background-repeat: no-repeat;
    background-size: contain;
}
.ui-button .icon.booking-icon {
    width: 0.38rem;
    height: 0.38rem;
    background-image: url("./images/common/booking-icon-38x38.png");
    background-repeat: no-repeat;
    background-size: contain;
}

/* 按钮样式end */
/* 公共列表页start */
.ui-list-mod-1 .list li {
  padding: 0.5rem;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.1rem;
}
.ui-list-mod-1 .list li .item dl {
    display: -webkit-box;
    font-size: 0.3rem;
    margin-bottom: 0.1rem;
}
.ui-list-mod-1 .list li .item dl dt {
      width: 1.6rem;
}
.ui-list-mod-1 .list li .item dl dd {
      -webkit-box-flex: 1;
}
.ui-list-mod-1 .list li .revoke-item {
    position: absolute;
    right: 0.3rem;
    bottom: 0.3rem;
}
.ui-list-mod-1 .list li .cancle-item {
    position: absolute;
    right: 0.3rem;
    bottom: 0.3rem;
}
.ui-list-mod-2 .list li {
  padding: 0.3rem;
  background-color: #fff;
  position: relative;
  margin-bottom: 0.16rem;
}
.ui-list-mod-2 .list li .item-wrap {
    display: -webkit-box;
    display: box;
}
.ui-list-mod-2 .list li .item-wrap .img-item {
      width: 2.7rem;
      height: 2.64rem;
}
.ui-list-mod-2 .list li .item-wrap .img-item .img {
        width: 100%;
        height: 100%;
        display: inline-block;
        background-size: contain;
        background-repeat: no-repeat;
        -o-object-fit: contain;
           object-fit: contain;
}
.ui-list-mod-2 .list li .item-wrap .txt-item {
      -webkit-box-flex: 1;
      box-flex: 1;
      margin: 0rem 0rem 0rem 0.54rem;
      font-size: 0.3rem;
      color: #70727c;
}
.ui-list-mod-2 .list li .item-wrap .txt-item .box-1 .txt-1 {
        display: inline-block;
        color: #000;
}
.ui-list-mod-2 .list li .item-wrap .txt-item .box-1 .txt-2 {
        display: inline-block;
        margin-left: 0.74rem;
}
.ui-list-mod-2 .list li .item-wrap .txt-item .box-2 .txt-1 {
        display: inline-block;
        vertical-align: middle;
}
.ui-list-mod-2 .list li .item-wrap .txt-item .box-2 .txt-1 .phone-txt {
          color: #3b91ff;
}
.ui-list-mod-2 .list li .item-wrap .txt-item .box-2 .phone-icon {
        width: 0.44rem;
        height: 0.44rem;
        display: inline-block;
        vertical-align: middle;
        margin: 0.05rem 0rem 0rem 0.1rem;
        background-image: url("./images/common/phone-icon-44x44.jpg");
        background-repeat: no-repeat;
        background-size: contain;
}
.ui-list-mod-2 .list li .booking-btn-item {
    position: absolute;
    right: 0.3rem;
    bottom: 0.2rem;
}

/* 公共列表页end */
/* 一像素start */
.ui-border-t {
  border-top: 1px solid #e0e0e0;
  /*no*/
}
.ui-border-b {
  border-bottom: 1px solid #e0e0e0;
  /*no*/
}
.ui-border-tb {
  border-top: #e0e0e0 1px solid;
  /*no*/
  border-bottom: #e0e0e0 1px solid;
  /*no*/
  background-image: none;
}
.ui-border-l {
  border-left: 1px solid #e0e0e0;
  /*no*/
}
.ui-border-r {
  border-right: 1px solid #e0e0e0;
  /*no*/
}
.ui-border {
  border: 1px solid #e0e0e0;
  /*no*/
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.ui-border {
    position: relative;
    border: 0;
}
.ui-border-t, .ui-border-b, .ui-border-l, .ui-border-r, .ui-border-tb {
    border: 0;
}
.ui-border:before {
    content: "";
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #e0e0e0;
    /*no*/
    -webkit-transform: scale(0.5);
    -webkit-transform-origin: 0 0;
    padding: 1px;
    /*no*/
    -webkit-box-sizing: border-box;
}
.ui-border-t {
    background-position: left top;
    background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0.5, transparent), color-stop(0.5, #e0e0e0), to(#e0e0e0));
}
.ui-border-b {
    background-position: left bottom;
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0.5, transparent), color-stop(0.5, #e0e0e0), to(#e0e0e0));
}
.ui-border-t, .ui-border-b {
    background-repeat: repeat-x;
    -webkit-background-size: 100% 1px;
    /*no*/
}
.ui-border-tb {
    background: -webkit-gradient(linear, left bottom, left top, color-stop(0.5, transparent), color-stop(0.5, #e0e0e0), to(#e0e0e0)) left top repeat-x, -webkit-gradient(linear, left top, left bottom, color-stop(0.5, transparent), color-stop(0.5, #e0e0e0), to(#e0e0e0)) left bottom repeat-x;
}
.ui-border-tb {
    -webkit-background-size: 100% 1px;
    /*no*/
}
.ui-border-l {
    background-position: left top;
    background-image: -webkit-gradient(linear, left top, right top, color-stop(0.5, transparent), color-stop(0.5, #e0e0e0), to(#e0e0e0));
}
.ui-border-r {
    background-position: right top;
    background-image: -webkit-gradient(linear, right top, left top, color-stop(0.5, transparent), color-stop(0.5, #e0e0e0), to(#e0e0e0));
}
.ui-border-l, .ui-border-r {
    background-repeat: repeat-y;
    -webkit-background-size: 1px 100%;
    /*no*/
}
}

/* 一像素end */
a {
  font-size: 0.18rem;
}



