
.g-footer {
  width: 100%;
  height: 1.3rem;
  line-height: 1.3rem;
  background-color: #fafafa;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 1;
}
.g-footer .item-wrap .list {
    display: -webkit-box;
    display: box;
}
.g-footer .item-wrap .list li {
      width: 3.6rem;
      -webkit-box-flex: 1;
      box-flex: 1;
      text-align: center;
}
.g-footer .item-wrap .list li .icon {
        width: 0.42rem;
        height: 0.42rem;
        display: inline-block;
        vertical-align: middle;
}
.g-footer .item-wrap .list li .icon.home-icon {
          background-image: url("../images/common/home-icon-42x40.png");
          background-repeat: no-repeat;
          background-size: contain;
}
.g-footer .item-wrap .list li .icon.booking-icon {
          background-image: url("../images/common/booking-icon-37x37.png");
          background-repeat: no-repeat;
          background-size: contain;
}
.g-footer .item-wrap .list li .icon.mine-icon {
          background-image: url("../images/common/mine-icon-35x35.png");
          background-repeat: no-repeat;
          background-size: contain;
}
.g-footer .item-wrap .list li .txt {
        display: inline-block;
        vertical-align: middle;
        color: #4a4a4a;
        font-size: 0.38rem;
        margin-left: 0.05rem;
}
.g-footer .item-wrap .list li.cur {
        position: relative;
}
.g-footer .item-wrap .list li.cur:after {
          content: "";
          position: absolute;
          width: 100%;
          height: 0.1rem;
          display: inline-block;
          background-color: #3b91ff;
          left: 0;
          top: 1px;
          z-index: 1;
}



