
.g-header {
  width: 100%;
  height: 1.45rem;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  background-color: #3b91ff;
}
.g-header .item-wrap .back-icon-item {
    width: 1.08rem;
    height: 1.45rem;
    position: absolute;
    left: 0rem;
    top: 0rem;
    z-index: 1;
    text-align: center;
}
.g-header .item-wrap .back-icon-item .back-icon {
      width: 0.5rem;
      height: 0.5rem;
      margin-top: 0.47rem;
      display: inline-block;
      background-image: url("../images/common/arrow-icon-50x50.png");
      background-repeat: no-repeat;
      background-size: contain;
}
.g-header .item-wrap .title-item {
    width: 60%;
    height: 1.45rem;
    line-height: 1.45rem;
    margin: 0 auto;
    color: #fff;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 0.42rem;
}



