
body .login-page {
  background-color: #4e8cde;
  padding-bottom: 100%;
}
body .login-page .item-wrap .p1th .title {
    margin-top: 1.4rem;
    color: #fff;
    font-size: 0.45rem;
}
body .login-page .item-wrap .p1th .content {
    height: 6.2rem;
    margin-top: 1.25rem;
    background-image: url("../../images/mine/login-bg-1080x540.jpg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left bottom;
}
body .login-page .item-wrap .p1th .content .input-item {
      width: 6.6rem;
      padding-left: 1rem;
      padding-right: 0.35rem;
      background-color: #fff;
      margin: 0rem auto 0.76rem;
}
body .login-page .item-wrap .p1th .content .input-item.user-name-item {
        background-image: url("../../images/mine/user-icon-37x37.jpg");
        background-repeat: no-repeat;
        background-size: contain;
        background-size: 0.37rem 0.37rem;
        background-position: 0.35rem center;
}
body .login-page .item-wrap .p1th .content .input-item.password-item {
        background-image: url("../../images/mine/password-icon-37x37.jpg");
        background-repeat: no-repeat;
        background-size: contain;
        background-size: 0.37rem 0.37rem;
        background-position: 0.35rem center;
}
body .login-page .item-wrap .p1th .content .input-item .input-txt {
        height: 0.94rem;
        width: 100%;
        font-size: 0.36rem;
}
body .login-page .item-wrap .p1th .content .btn-item {
      width: 7.98rem;
      height: 0.93rem;
      line-height: 0.93rem;
      margin: 1.9rem auto 0rem;
      background-color: #31b434;
}
body .login-page .item-wrap .p1th .content .btn-item .icon {
        width: 0.52rem;
        height: 0.4rem;
        display: inline-block;
        vertical-align: middle;
        background-image: url("../../images/mine/login-icon-52x40.jpg");
        background-repeat: no-repeat;
        background-size: contain;
}
body .login-page .item-wrap .p1th .content .btn-item .txt {
        font-size: 0.4rem;
        display: inline-block;
        vertical-align: middle;
        color: #fff;
        margin-left: 0.2rem;
}



